import React from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { HcmutLogo, ErrorIcon } from "../../Assets/Icons/Icons";
import { useNavigate } from "react-router-dom";
import "./Login.scss";

const Login = () => {
  const schema = yup.object().shape({
    TenTaiKhoan: yup.string().required("Tn ti khon l bt buc"),
    MatKhau: yup.string().required("Mt khu l bt buc"),
  });

  const navigate = useNavigate();

  const {
    reset,
    handleSubmit,
    register,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
  });

  const onSubmit = (data) => {
    console.log(data);
    setTimeout(() => {
      if (!Object.keys(errors).length) {
        navigate("/Home");
      }
    }, 1500);
  };

  const handleReset = () => {
    reset();
  };

  return (
    <>
      <div className="Wrapperlogin w-[85%] bg-white mx-auto mt-2">
        <div className="Title w-full h-[70px] bg-[#210F7A] flex items-center pl-[5px]">
          <HcmutLogo></HcmutLogo>
          <div className="text-white font-bold text-[26px] ml-[24px] tracking-widest">
            DCH V XC THC TP TRUNG
          </div>
        </div>
        <div className="flex items-start wrapperBox">
          <div className="formContainer ml-[10px] my-[10px] bg-[#eeeeee] w-[420px] p-[20px]">
            {Object.keys(errors).length > 0 && (
              <div className="w-full py-3 rounded-lg bg-[#ffeedd] text-[#bb0000] border border-red-400 text-center flex gap-2 items-center pl-4">
                <ErrorIcon></ErrorIcon>
                <div className="pl-3">
                  <p> {errors?.TenTaiKhoan?.message}</p>
                  <p> {errors?.MatKhau?.message}</p>
                </div>
              </div>
            )}
            <div className="font-bold text-[#990033] text-[18px] border-b-2 border-[#990033] tracking-widest">
              Nhp thng tin ti khon ca bn
            </div>
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="mt-[10px]">
                <label
                  htmlFor="TenTaiKhoan"
                  className="block font-semibold text-[#777777]"
                >
                  Tn ti khon
                </label>
                <input
                  {...register("TenTaiKhoan")}
                  name="TenTaiKhoan"
                  id="TenTaiKhoan"
                  type="text"
                  className="h-[30px] w-[314px] px-2 outline-none border border-[#dddddd] bg-[#ffffdd] rounded-[3px]"
                />
              </div>
              <div className="mt-[10px]">
                <label
                  htmlFor="MatKhau"
                  className="block font-semibold text-[#777777]"
                >
                  Mt khu
                </label>
                <input
                  {...register("MatKhau")}
                  name="MatKhau"
                  id="MatKhau"
                  type="password"
                  className="h-[30px] w-[314px] px-2 outline-none border border-[#dddddd] bg-[#ffffdd] rounded-[3px]"
                />
              </div>
              <div className="mt-[10px] flex items-start border-b-2 border-[#990033] pb-[10px]">
                <input type="checkbox" className="s translate-y-[2px]" />
                <span className="text-[#777777] text-[14px] pl-2 tracking-widest">
                  Cnh bo trc khi ti ng nhp vo cc trang web khc.
                </span>
              </div>
              <div className="flex gap-1 my-[10px]">
                <button
                  type="submit"
                  className="bg-[#006dcc] text-white rounded-[4px] outline-none px-[14px] py-[4px] text-[14px]"
                >
                  ng nhp
                </button>
                <button
                  type="button"
                  onClick={handleReset}
                  className="bg-[#006dcc] text-white rounded-[4px] outline-none px-[14px] py-[4px] text-[14px]"
                >
                  Xa
                </button>
              </div>
              <a
                href="https://account.hcmut.edu.vn/"
                className="text-[#0000ee] text-[14px]"
              >
                Thay i mt khu?
              </a>
            </form>
          </div>
          <div className="mt-[30px] ml-[20px]">
            <h2 className="text-[#990033] text-[16px] tracking-wider font-semibold">
              Ngn ng
            </h2>
            <div>
              <span className="text-[#0000ee] cursor-pointer text-[14px] underline">
                Ting Vit
              </span>
              <span className="mx-[10px]">|</span>
              <span className="text-[#0000ee] cursor-pointer text-[14px] underline">
                Ting Anh
              </span>
            </div>
            <h2 className="text-[#990033] text-[16px] mt-[10px] tracking-wider font-semibold">
              Lu 
            </h2>
            <p className="text-[14px] px-[20px]">
              Trang ng nhp ny cho php ng nhp mt ln n nhiu h thng
              web  trng i hc Bch Khoa Tp.HCM.iu ny c ngha l bn ch
              ng nhp mt ln cho nhng h thng web  ng k vi h thng
              xc thc qun l truy cp tp trung.
            </p>

            <p className="text-[14px] px-[20px]">
              Bn cn dng ti khon HCMUT  ng nhp. Ti khon HCMUT cho
              php truy cp n nhiu ti nguyn bao gm h thng thng tin, th
              in t, ...
            </p>
            <p className="text-[14px] px-[20px]">
              V l do an ninh, bn hy Thot khi trnh duyt Web khi bn kt
              thc vic truy cp cc dch v i hi xc thc!
            </p>
            <h2 className="text-[#990033] text-[16px] mt-[15px] tracking-wider font-semibold">
              H tr k thut
            </h2>
            <div>
              <div className="text-[14px] px-[5px]">
                Email:
                <a
                  href="mailto:support@hcmut.edu.vn"
                  className="text-[#0000ee]  text-[14px] ml-[10px] underline"
                >
                  support@hcmut.edu.vn
                </a>
                <span className="mx-[10px]">|</span>
                <a href="/" className="text-[14px] underline">
                  T: (84-8) 38647256 - 5200
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="w-[85%] mx-auto text-[14px] mt-3 text-[#777777]">
        Bn quyn  2011 - 2012 i hc Bch Khoa Tp. H Ch Minh. c h tr
      </div>
      <div className="w-[85%] mx-auto text-[14px] text-[#777777]">
        bi
        <a
          href="https://www.apereo.org/projects/cas"
          className="text-[#0000ee] ml-[3px]"
        >
          Jasig CAS 3.5.1
        </a>
      </div>
    </>
  );
};

export default Login;
